<template>
  <section class="event-info" v-if="getSingleEvent.what">
      <article class="single-event-info" >
          <h3> What: {{getSingleEvent.what}}</h3>
            <details>
              <summary><p> Where-venue: <b> {{ getSingleEvent.where.venue}} </b> </p></summary> 
                <p> Adress: <small> <b> {{ getSingleEvent.where.adress}} </b> </small>  </p> 
                <p> Date: <b> {{getSingleEvent.when.date}} </b> </p> 
                <p> From: <b> {{getSingleEvent.when.from}} </b> - To: <b>{{getSingleEvent.when.to}} </b> </p>
                <p> Price:<b> {{getSingleEvent.price}}</b>  <i>kr. </i>  </p> 
                <p> Info: <b> {{getSingleEvent.info}} </b>  </p> 
                <p><small> Id: <b>{{getSingleEvent.ticketID}}</b></small> </p>
            </details>
      </article>
      <article class="single-event-btn">
          <button class="btn-buy" @click="toTicketRoute">Buy !</button>
      </article>
  </section>

  <div v-else> Nothing to show! bad request</div>
</template>

<script>
export default {
  name:'EventInfo',
  mounted(){
    this.$store.dispatch('getAllEventsById',this.$route.params.id);
  },
  computed:{
    getSingleEvent(){
      return this.$store.state.singleEvent;
    },
  },
  methods:{
    toTicketRoute(){
      this.$router.push('/ticket');
      this.setTicketMutation(this.ticket);
    },
    setTicketMutation(){
      this.$store.dispatch('booktTicketAction');
    }
  }
}
</script>

<style lang="scss">
  .event-info{
    font-size: 1.75vw;
    flex-direction: column;
    align-items: center;
    border-style: solid;
  
      .single-event-info{
        
        width:auto;
        max-width:100%;
        min-width:100%;
        padding-bottom: 0.1rem;
        
          h3{
            margin:0px;
            padding-top: 1.83rem;
            background-color: rgb(111, 228, 177);
            height:10vh;
          }
      }
      .btn-buy{
            display: inline-block;
            width: 100%;
            background-color: rgb(26, 18, 84);
            color: white;
            height:10vh;
            margin: 0px;
            cursor: pointer;
            font-size: 1.3rem;
            font-family: 'Langar', cursive;
      }
      .btn-buy:hover{
            background-color: rgba(49, 40, 120, 0.776);
      }
  }
</style>